/*investmens - инвесторам*/
.contracts {
  line-height: 2; }

.calc h4 {
  margin-top: 0;
  margin-bottom: 3rem; }

.calc-programms {
  display: flex;
  flex-wrap: wrap; }
  .calc-programms label {
    display: inline-block;
    margin-bottom: 2rem; }
    .calc-programms label small {
      display: block;
      margin-left: 2.8rem;
      opacity: 0.5;
      font-size: 1rem; }
    .calc-programms label::before {
      display: inline-block;
      content: '';
      width: 2.4rem;
      height: 2.4rem;
      box-shadow: 0 5px 10px rgba(0, 0, 1, 0.1);
      background-color: #ffffff;
      vertical-align: middle;
      border: 0.6rem solid #ffffff;
      border-radius: 50%;
      transition: all 0.2s; }
    .calc-programms label:hover {
      cursor: pointer; }

.calc input[type="radio"] {
  display: none; }
  .calc input[type="radio"]:checked + label::before {
    background-color: #009adf;
    border: 0.6rem solid #ffffff; }

.calc-radio {
  flex-basis: 50%; }
  .calc-radio:last-child {
    flex-basis: 100%; }

.calc-legal {
  font-size: 1.2rem;
  margin-bottom: 3rem; }

.calc-popular {
  display: inline-block;
  justify-content: center;
  align-items: right;
  font-size: 1rem;
  text-transform: uppercase;
  padding: 0 3rem;
  height: 2.5rem;
  margin-left: 1.6rem;
  color: #ffffff;
  background-color: #009adf;
  position: relative;
  max-width: 32%;
  min-width: 13rem; }
  .calc-popular::before {
    content: '';
    width: 0.6rem;
    height: 1.3rem;
    border: 0.6rem solid transparent;
    border-right: 0.6rem solid #009adf;
    position: absolute;
    left: -1.2rem;
    top: 50%;
    margin-top: -0.6rem; }

.calc input[type="range"] {
  appearance: none;
  width: 90%;
  height: 0.5rem;
  background-color: #131313;
  opacity: 0.3;
  outline: none; }
  .calc input[type="range"]::-webkit-slider-thumb {
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 50%;
    background: #009adf; }
  .calc input[type="range"] input[type="range"]::-ms-fill-lower {
