/*всплывающие окна*/
.modal {
  display: none;
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  left: 0;
  top: 0;
  height: 100vh;
  width: 100vw; }
  .modal-content {
    position: relative;
    background-color: #ffffff;
    width: 100%;
    max-width: 370px;
    margin: 10% auto;
    text-align: center;
    padding: 3rem 4rem;
    box-shadow: 0 1.2rem 2.4rem rgba(0, 0, 1, 0.2); }
    .modal-content .modal-close {
      position: absolute;
      top: 0;
      right: 0;
      padding: 1rem 1.5rem;
      font-size: 3rem;
      opacity: 0.5; }
      .modal-content .modal-close:hover {
