@media (min-width: 768px) and (max-width: 992px) {
  .top-nav_btn {
    display: block;
    outline: 0 !important; }
  .top-nav_menu {
    display: none;
    margin-top: 1.5rem; }
    .top-nav_menu li {
      line-height: 2;
